#!/usr/bin/env node

import { Command } from 'commander';
import path from 'path';
import { readFileSync } from 'jsonfile';
import { parse } from 'url';
import { v4 as uuid } from 'uuid';
import paramCase from 'param-case';

import runTest from '../tester';
import { CHROMATIC_CREATE_TUNNEL, CHROMATIC_APP_CODE } from '../assets/environment';

// Ensure NODE_ENV is set
process.env.NODE_ENV = process.env.NODE_ENV || 'test';

// This is not exactly clever but it works most of the time
export function findOption(storybookScript, shortName, longName) {
  const parts = storybookScript.split(/[\s='"]+/);
  let index = parts.indexOf(longName);
  if (index === -1) {
    index = parts.indexOf(shortName);
  }
  if (index === -1) {
    return null;
  }
  return parts[index + 1];
}

export function parseArgv(argv) {
  const commander = new Command()
    .option('-a, --app-code <code>', 'the code for your app, get from chromaticqa.com')

    .option(
      '-b, --build-script-name [name]',
      'The npm script that builds your storybook [build-storybook]'
    )
    .option(
      '-d, --storybook-build-dir <dirname>',
      "Provide a directory with your built storybook; use if you've already built your storybook"
    )

    .option('-s, --script-name [name]', 'The npm script that starts your storybook [storybook]')
    .option('-e, --exec <command>', 'Alternatively, a full command to run to start your storybook.')
    .option(
      '-S, --do-not-start',
      "Don't attempt to start or build; use if your storybook is already running"
    )

    .option(
      '--storybook-https',
      'Use if Storybook is running on https (auto detected from -s, if set)?'
    )
    .option(
      '-p, --storybook-port <port>',
      'What port is your Storybook running on (auto detected from -s, if set)?'
    )
    .option(
      '-u, --storybook-url <url>',
      'Storybook is already running at (external) url (implies -S)'
    )

    .option('--ci', 'This build is running on CI, non-interactively (alternatively, pass CI=true)')
    .option(
      '--auto-accept-changes [branch]',
      'Accept any (non-error) changes or new stories for this build [only for <branch> if specified]'
    )
    .option(
      '--exit-zero-on-changes [branch]',
      "Use a 0 exit code if changes are detected (i.e. don't stop the build) [only for <branch> if specified]"
    )
    .option(
      '--ignore-last-build-on-branch [branch]',
      'Do not use the last build on this branch as a baseline if it is no longer in history (i.e. branch was rebased) [only for <branch> if specified]'
    )
    .option(
      '--preserve-missing',
      'Treat missing stories as unchanged (as opposed to deleted) when comparing to the baseline'
    )
    .option('--no-interactive', 'Do not prompt for package.json changes')

    .option(
      '--only <component:story>',
      'Only run a single story or a glob-style subset of stories (for debugging purposes)'
    )
    .option('--list', 'List available stories (for debugging purposes)')
    .option('--debug', 'Output more debugging information')

    // We keep this for back compat it does nothing (ie. it is the default)
    .option('--storybook-addon', '(deprecated) use the storybook addon')
    .parse(argv);

  const commanderOptions = {
    config: commander.config,
    appCode: commander.appCode || CHROMATIC_APP_CODE,
    buildScriptName: commander.buildScriptName,
    scriptName: commander.scriptName,
    exec: commander.exec,
    noStart: !!commander.doNotStart,
    https: commander.storybookHttps,
    port: commander.storybookPort,
    storybookUrl: commander.storybookUrl,
    storybookBuildDir: commander.storybookBuildDir,
    only: commander.only,
    list: commander.list,
    fromCI: !!commander.ci,
    autoAcceptChanges: commander.autoAcceptChanges,
    exitZeroOnChanges: commander.exitZeroOnChanges,
    ignoreLastBuildOnBranch: commander.ignoreLastBuildOnBranch,
    preserveMissingSpecs: commander.preserveMissing,
    interactive: !!commander.interactive,
    verbose: !!commander.debug,
    createTunnel: !commander.storybookUrl && CHROMATIC_CREATE_TUNNEL !== 'false',
    originalArgv: argv,
  };

  const packageJson = readFileSync(path.resolve('./package.json'));
  const { storybookBuildDir, exec } = commanderOptions;
  let { https, port, storybookUrl, noStart, scriptName, buildScriptName } = commanderOptions;

  // We can only have one of these arguments
  const singularCommands = [
    'buildScriptName',
    'scriptName',
    'exec',
    'storybookUrl',
    'storybookBuildDir',
  ].filter(name => !!commanderOptions[name]);
  if (singularCommands.length > 1) {
    throw new Error(
      `Can only use one of ${singularCommands.map(n => `--${paramCase(n)}`).join(', ')}`
    );
  }

  // Do we serve or build?
  const serve = !!scriptName || exec || noStart || storybookUrl || port;

  if (!serve) {
    if (storybookBuildDir) {
      return { ...commanderOptions, noStart: true };
    }

    buildScriptName = typeof buildScriptName === 'string' ? buildScriptName : 'build-storybook';
    const buildScript = packageJson.scripts && packageJson.scripts[buildScriptName];
    if (!buildScript) {
      throw new Error(
        `Chromatic Tester: Didn't find a script called '${buildScriptName}' in your \`package.json\`.\n` +
          'Make sure you set the `--build-script-name` option to the value of the npm script that builds your storybook'
      );
    }

    return { ...commanderOptions, noStart: true, buildScriptName };
  }

  if (storybookUrl) {
    noStart = true;
  } else {
    if (exec) {
      if (!port) {
        throw new Error(`You must pass a port with the --storybook-port option when using --exec.`);
      }
      // If you don't provide a port or we need to start the command, let's look it up
    } else if (!noStart || !port) {
      scriptName = typeof scriptName === 'string' ? scriptName : 'storybook';
      const storybookScript = packageJson.scripts && packageJson.scripts[scriptName];

      if (!storybookScript) {
        throw new Error(
          `Chromatic Tester: Didn't find a script called '${scriptName}' in your \`package.json\`.\n` +
            'Make sure you set the `--script-name` option to the value of the npm script that starts your storybook'
        );
      }

      https = https || findOption(storybookScript, '--https') || false;
      port = port || findOption(storybookScript, '-p', '--port');
      if (!port) {
        throw new Error(
          `Didn't detect a port in your '${scriptName}' script. You must pass a port with the --storybook-port option.`
        );
      }

      // eslint-disable-next-line no-console
      console.log(
        `Chromatic Tester: Detected '${scriptName}' script, running with inferred options:
    --script-name=${scriptName} --storybook-port=${port}
  Override any of the above if they were inferred incorrectly.
  `
      );
    }

    storybookUrl = `${https ? 'https' : 'http'}://localhost:${port}`;
  }

  const parsedUrl = parse(storybookUrl);
  const suffix = 'iframe.html';
  if (!parsedUrl.pathname.endsWith(suffix)) {
    if (!parsedUrl.pathname.endsWith('/')) {
      parsedUrl.pathname += '/';
    }
    parsedUrl.pathname += suffix;
  }

  return { ...commanderOptions, noStart, url: parsedUrl.format(), scriptName };
}

export async function executeTest(argv) {
  const sessionId = uuid();
  try {
    const exitCode = await runTest({
      ...parseArgv(argv),
      sessionId,
    });
    process.exit(exitCode);
  } catch (error) {
    console.error(
      `**Chromatic build failed. Please note the session id: '${sessionId}' and contact support@hichroma.com -or- open a support ticket at https://chromaticqa.com**\n`
    );
    if (error.length) {
      // eslint-disable-next-line no-console
      // This is a GraphQL Error, our server is reasonable
      error.map(e => console.error(e.message));
    } else {
      // eslint-disable-next-line no-console
      console.error(error);
    }
    console.log();
    // Not sure what exit code to use but this can mean error.
    process.exit(255);
  }
}

// Normal usage, outside of test
if (require.main === module) {
  executeTest(process.argv);
}
